export function yAxisFormatter(value) {
  let stringLen = String(value).length;
  if (stringLen > 8) {
    return value / 100000000 + '亿';
  } else if (stringLen == 8) {
    return value / 10000000 + '千万';
  } else if (stringLen >= 5) {
    return value / 10000 + '万';
  } else {
    return value;
  }
}

export function getLineColor(baseColor, point) {
  let afterColor = lightenDarkenColor(baseColor, 64);
  return {
    color: {
      type: 'linear',
      x: 0,
      y: 0,
      x2: 1,
      y2: 0,
      colorStops: [
        {
          offset: 0,
          color: baseColor // 0% 处的颜色
        },
        {
          offset: point,
          color: baseColor // point 处的颜色
        },
        {
          offset: point,
          color: afterColor // point 处的颜色
        },
        {
          offset: 1,
          color: afterColor // 100% 处的颜色
        }
      ]
    }
  };
}

export function lightenDarkenColor(col, amt) {
  var usePound = false;
  if (col[0] == '#') {
    col = col.slice(1);
    usePound = true;
  }

  var num = parseInt(col, 16);

  var r = (num >> 16) + amt;

  if (r > 255) r = 255;
  else if (r < 0) r = 0;

  var b = ((num >> 8) & 0x00ff) + amt;

  if (b > 255) b = 255;
  else if (b < 0) b = 0;

  var g = (num & 0x0000ff) + amt;

  if (g > 255) g = 255;
  else if (g < 0) g = 0;

  return (usePound ? '#' : '') + (g | (b << 8) | (r << 16)).toString(16);
}
